// 引入组件
import deviceInfo from '@/views/equipment/EquipmentStatus_andon/deviceInfo.vue';
import deviceTable from '@/views/equipment/EquipmentStatus_andon/deviceTable.vue';
import HeaderTitle from '@/components/common/PageNavBar/HeaderTitle';
import { EQUIPMENT_LIVE, BTN_TEXT } from '@/utils/constantsFile';
import HeadOperationItem from '@/components/common/PageNavBar/HeadOperationItem';
import SearchBox from '@/components/common/SearchBox';
export default {
  name: 'EquipmentStatus_andon',
  data() {
    return {
      // 搜索框有关
      isSearchBoxShow: false,
      // tab默认激活下标
      BTN_TEXT: BTN_TEXT,
      EQUIPMENT_LIVE: EQUIPMENT_LIVE,
      active: 0,
      // rows: {},
      showFlag: 0,
      deviceData: {},
      hideMain: true,
      // 默认引入的组件名称
      currentView: 'deviceTable',
      dialogVisible: false
    };
  },
  created() {
    if (this.$store.state.deviceIsJump === true) {
      this.showInfo(1, this.$store.state.deviceJump);
    }
  },
  computed: {
    monitorBtn() {
      return this.$store.state.monitorTags;
    }
  },
  watch: {
    monitorBtn: function() {
      var that = this;
      this.showFlag = 5;
      setTimeout(() => {
        that.$store.state.deviceIsJump = false;
        that.showInfo(1, this.$store.state.deviceJump);
      }, 0);
    }
  },
  methods: {
    getUrlKeyMain(name, url) {
      // eslint-disable-next-line no-sparse-arrays
      return decodeURIComponent((new RegExp('[?|&]' + name + '=' + '([^&;]+?)(&|#|;|$)').exec(url) || [, ''])[1].replace(/\+/g, '%20')) || null;
    },
    /**
     * @description: （主页面）点击“搜索”按钮，打开搜索框
     * @param 无
     * @return: void
     * @author: cyb
     */
    showSearchCard() {
      // alert(1321);
      this.isSearchBoxShow = !this.isSearchBoxShow;
    },
    /**
     * @description:显示详情也卖弄
     * @param {type}
     * @return:
     * @author: CH
     */
    showInfo(showFlag, row) {
      this.showFlag = showFlag;
      this.deviceData = row;
      this.hideMain = false;
    },
    /**
     * @description:返回主页面
     * @param {type}
     * @return:
     * @author: CH
     */
    back() {
      this.showFlag = 0;
      this.hideMain = true;
    },
    /**
     * @description:导出
     * @param {type}
     * @return:
     * @author: CH
     */
    exportTable() {
      if (this.active === 0) {
        this.$refs.deviceTable.exportTable();
      } else {
        this.$refs.warningTable.exportTable();
      }
    },
    /**
     * @description:   刷新
     * @param {type}
     * @return:
     * @author: CH
     */
    refresh() {
      this.$refs.deviceTable.findList();
    }
  },
  components: {
    // 加载未审核组件
    deviceTable,
    HeaderTitle,
    deviceInfo,
    HeadOperationItem,
    SearchBox
  }
};
